import {mapState,mapMutations,mapGetters } from 'vuex';
export default {
  name:'register',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      mp:'',//手机号
      yzm:'',//验证码
      mpRegular:/^1[3|4|5|6|7|8]\d{9}$/, //验证手机号
      mpRegular:/^1[3|4|5|6|7|8]\d{9}$/, //验证手机号
      yzmText:'获取验证码',//验证码按钮文案
      code:0,//倒计时剩余秒数
      isOk:false//是否阅读
    }
  },
  computed:{
    ...mapState(['RegistrationAgreement','errorText']),
    ...mapGetters(['domain']),
  },
  methods:{
    ...mapMutations(['errorEnv']),
    //请求验证码
    verifyCode(){
      if(this.mp==''){
        this.$vux.alert.show({
          title: '提示',
          content: '手机号不能为空',
          //显示时加载
          onShow () {  },
          //隐藏时加载
          onHide () {  }
        })

      }else if(!this.mpRegular.test(this.mp)){
        this.$vux.alert.show({
          title: '提示',
          content: '请输入正确的手机号'
        });
	   	}else if(this.code>0){
        this.$vux.alert.show({
          title: '提示',
          content: '60秒内只允许获取一次验证码'
        })
      }else{
        this.$vux.loading.show({
            text: '验证码获取中'
        })
        this.$ajax({
          method: 'get',
          //url:'static/test/verifyCode.json', //<---本地地址
          url: `${API_ORIGIN}/v2/sms/verifyCode?type=1&mobile=`+this.tel,
        }).then(response=>{
            //setTimeout(()=>this.$vux.loading.hide(), 1000);
            this.$vux.loading.hide()
            this.code=60;
            this.timer();
        }).catch(function(err){
            this.$vux.loading.hide()
            this.errorEnv(err);
            this.$vux.alert.show({
              title: '提示',
              content: this.errorText
            });
        })
      }
    },
    //倒计时
    timer() {
      if (this.code > 0) {
        this.yzmText=this.code+'s';
        this.code--;
        setTimeout(this.timer, 1000);
      }else{
        this.yzmText='获取验证码';
      }
    },
    //注册请求
    register(){
      if(!this.mp){
        this.$vux.alert.show({
          title: '提示',
          content: '手机号不能为空'
        })
  		}else if(!this.yzm){
        this.$vux.alert.show({
          title: '提示',
          content: '验证码不能为空'
        })
  		}else if(!/^1[3|4|5|6|7|8]\d{9}$/.test(this.mp)){
        this.$vux.alert.show({
          title: '提示',
          content: '请输入正确的手机号'
        })
	   	}else if(!/^\d{6}$/.test(this.yzm)){
        this.$vux.alert.show({
          title: '提示',
          content: '请输入6位数字密码'
        })
	   	}else if(this.isOk==false){
        this.$vux.alert.show({
          title: '提示',
          content: '请阅读并同意《用户注册须知》'
        })
  		}else{
        this.$vux.loading.show({
            text: '注册中'
        })
        //this.$router.push(this.domain + '/v2/weixin/code/url?phoneNum='+this.mp+'&verifyCode='+this.yzm+'&from=0');
        window.location.href=`${API_ORIGIN}/v2/weixin/code/url?phoneNum=`+this.mp+'&verifyCode='+this.yzm+'&from=0';
        setTimeout(()=>this.$vux.loading.hide(), 1000);
  		}
    },
    //协议阅读状态
    read(){
      if(this.isOk){
        this.isOk=false
      }else{
        this.isOk=true
      }
    },
    //唤起协议弹窗
    agreement(){
      let _this=this
      this.$vux.confirm.show({
        title: '用户注册须知',
        content: this.RegistrationAgreement,
        onCancel () {
          //取消时操作
          _this.isOk=false
        },
        onConfirm () {
          //确定时操作
          _this.isOk=true
        }
      })
    }

  }
}



